import React from 'react';
import styles from "./Columns.module.css"

function Columns(props) {
    return (
        <div className={styles.columns} >
            <div className={styles.columns_left} >
            {props.left}
            </div>
            <div className={styles.columns_right} >
                {props.right}
            </div>
        </div>
    )
}

export default Columns;
