import React from 'react';
import styles from "./Main.module.css";
import Titolo from "../../Utilita/Titolo"

import Card from './Card/Card';
import Pricing from './Pricing/Pricing';
import Footer from './Footer/Footer';

import Collect from "./collect.jpg"
import Analyze from "./analyze.jpg"
import Use from "./build.jpg"

function Main() {
    return (
        <div className={styles.main} >
            <Titolo size="1.5rem" text="Getting the most out of the least" />
            <div className={styles.main_card_container} >
                <Card img={Collect} title="You collect" />
                <Card img={Analyze} title="We analyze" />
                <Card img={Use} title="We build and you use" />
            </div>
            <Pricing />
            <Footer />
        </div>
    )
}

export default Main;
