import React from 'react';
import { Link } from "react-router-dom"

import Input from "../Utilita/Input"
import Button from '../Utilita/Button';
import Titolo from '../Utilita/Titolo';

function Login() {
    return (
        <form className="form_login" >
            <Titolo size="2rem" text="Log into your account" />
                
                <Input type="email" placeholder="Email" />
                <Input type="password" placeholder="Password" />

                <p> Don't have an account yet? Create one <Link to="/register">here</Link></p>
                <label>Forgot password?</label>

                <Button> Create my account </Button>
        </form>
    )
}

export default Login;
