import React from 'react';

function Titolo(props) {
    const { text, size, color } = props
    return (
        <h1
        style={{ fontWeight: "bolder", fontSize: size || "2rem", fontFamily: "'Raleway', sans-serif", color: color || "black" }}
        > { text } </h1>
    )
}

export default Titolo;
